import React from 'react'
import { Route, Switch } from 'react-router-dom'
import Homepage from './Pages/Home/Home'
import Music from './Pages/Music/Music'
import Tours from './Pages/Tours/Tours'

const Routes = () => {
  const baseName = process.env.PUBLIC_URL

  return (
    <div>
    <Route exact={true} path={`${baseName}/`} component={Homepage} />
    <Route sensitive path={`${baseName}/music`} component={Homepage} />
    <Route sensitive path={`${baseName}/tour`} component={Homepage} />
    <Route sensitive path={`${baseName}/merch`} component={Homepage} />
    <Route sensitive path={`${baseName}/about`} component={Homepage} />

    
    </div>
  )
}

export default Routes